// Tabulator 6.3.1     
import { TabulatorFull as Tabulator } from 'tabulator-tables';

/**
 * Tabulator   
 * @param {HTMLElement} element -   DOM 
 * @param {Array} cols -    
 * @param {Array} data -    
 * @param {Object} [customOptions] -    (  )
 * @returns {Tabulator} -  Tabulator 
 */
export function createTable(element, cols, data, customOptions = {}) {
  // cellStyle  
  const applyCellStyle = (cell, style) => {
    if (style && typeof style === 'object') {
      const cellElement = cell.getElement();
      Object.keys(style).forEach((styleKey) => {
        cellElement.style[styleKey] = style[styleKey];
      });
    }
    return cell.getValue() !== undefined && cell.getValue() !== null ? cell.getValue() : '';
  };

  //    cellStyle  
  const processedCols = cols.map((col) => {
    if (col.cellStyle && typeof col.cellStyle === 'object') {
      const originalFormatter = col.formatter;
      const { cellStyle, ...rest } = col; // Destructure only if cellStyle exists
      return {
        ...rest,
        formatter: (cell) => {
          applyCellStyle(cell, cellStyle);
          return originalFormatter ? originalFormatter(cell) : cell.getValue();
        },
      };
    }
    return { ...col }; // Return a new object to avoid mutating the original
  });

  //    (Tabulator 6.3.1 )
  const defaultOptions = {
    // ---   ---
    height: '50vh',                  //  . (px), (%),  (vh) . undefined  
    layout: 'fitColumns',            //  . 'fitColumns'      . : 'fitData', 'fitDataFill', 'fitDataStretch'
    layoutColumnsOnNewData: false,   //        . false   
    responsiveLayout: false,         //   .    . : 'hide', 'collapse'
    responsiveLayoutCollapseStartOpen: true, //     . true  
    autoResize: true,                //      . true    
    renderVertical: 'virtual',       //   . 'virtual'  DOM , 'basic'  
    renderHorizontal: 'virtual',     //   . 'virtual'  DOM , 'basic'  

    // ---   ---
    data: data || [],                //  .     
    reactiveData: false,             //   . true      (:  )
    dataTree: false,                 //    . true   
    dataTreeStartExpanded: false,    //    . false   
    columns: processedCols,          //  .     
    autoColumns: false,              //     . true     
    movableColumns: true,            //    . true     
    resizableColumns: true,          //     . true    
    headerVisible: true,             //   . false  
    headerSort: true,                //    . true  
    headerFilterLiveFilterDelay: 300, //      (ms).     

    // ---   ---
    movableRows: true,               //    . true     
    resizableRows: false,            //     . false   
    selectable: true,                //    . true /   ( 'highlight' )
    selectableRollingSelection: true, //   . true Shift    
    selectablePersistence: true,     //   . true     

    // ---  ---
    pagination: 'local',             //  . 'local'  , 'remote'  . false 
    paginationSize: 50,              //   .   
    paginationButtonCount: 10,       //    .    
    paginationSizeSelector: [50, 100, 200, 300], //    .   
    paginationInitialPage: 1,        //   . 1 

    // ---  ---
    placeholder: ' .',      //     .    

    // ---   ---
    columnMoved: (column, columns) => {
      console.log('Column moved:', column.getField(), columns.map(col => col.getField()));
    },                               //    .      
  };

  //      
  const options = { ...defaultOptions, ...customOptions, data };

  try {
    const table = new Tabulator(element, options);
    return table;
  } catch (error) {
    console.error('Tabulator  :', error);
    throw error; //   
  }
}