import * as XLSX from "xlsx";

/**
 * Tabulator      
 * @param {Object} tableInstance - Tabulator   (Tabulator )
 * @param {string} tableStatus -   ("initializing", "ready", "error")
 * @param {string} [fileName="table_data.xlsx"] -     (: table_data.xlsx)
 * @param {string} [sheetName="Sheet1"] -    (: Sheet1)
 */
export const handleDownloadExcel = (
  tableInstance,
  tableStatus,
  fileName = "table_data.xlsx",
  sheetName = "Sheet1"
) => {
  //    
  // tableInstance   "ready"   
  if (!tableInstance || tableStatus !== "ready") {
    console.error("Table instance not ready:", tableInstance, tableStatus);
    return;
  }

  try {
    //    
    const data = tableInstance.getData();
    //       
    if (!data || data.length === 0) {
      console.warn("No data available to download");
      return;
    }

    //    
    const columns = tableInstance.getColumns();
    //     (: ["ID", "", "", ""])
    const headers = columns.map(col => col.getDefinition().title);
    //    (: ["id", "name", "age", "status"])
    const fields = columns.map(col => col.getField());

    //  2D  
    //    ( ),   
    const aoaData = [
      headers, //   
      ...data.map(row => fields.map(field => row[field])), //   
    ];

    // 2D   
    const worksheet = XLSX.utils.aoa_to_sheet(aoaData);
    //   
    const workbook = XLSX.utils.book_new();
    //   
    XLSX.utils.book_append_sheet(workbook, worksheet, sheetName);
    //   
    XLSX.writeFile(workbook, fileName);
  } catch (err) {
    //       
    console.error("Excel download failed:", err);
  }
};